<template>
  <div class="WeddingList">
    <div style="display: block; height: 70px;"></div>
    <input @click="getData" type="button" value="Get Data">
    <div id="content"></div>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  name: 'WeddingList',
  data () {
    return { content: {} }
  },
  methods: {
    getData () {
      axios('https://spreadsheets.google.com/feeds/list/1tFcDbWFRTYtHbs4opQx-qz18hxZS6myCFko6RUnZrPk/od6/public/values?alt=json', {
        method: 'GET'
      }).then((response) => {
        this.content = this.getAndToObj(response)
        var DataObj = this.content
        console.log(DataObj)
        var el = document.querySelector('#content')
        for (var row in DataObj) {
          for (var li in DataObj[row]) {
            el.innerHTML += ' ' + DataObj[row][li]
          }
          el.innerHTML += '<br>'
        }
      })
    },
    getAndToObj (response) {
      var Obj = response.data.feed.entry
      var ObjNameStrok = this.parsToNameStrok(response)

      var row = 0
      var name = ''

      var GObj = {}
      var mas = []
      for (var key in ObjNameStrok) {
        mas = []
        for (var st in ObjNameStrok[key]) {
          row = key
          name = ObjNameStrok[key][st]
          mas.push(Obj[row][name].$t)
        }
        GObj[key] = mas
      }
      return GObj
    },
    parsToNameStrok (data) {
      var arrData = data.data.feed.entry
      var myRe = new RegExp('gsx\\$', 'g')
      var masGsx = []
      var ItogObg = {}
      var temp = []
      for (var i = 0; i < arrData.length; i++) {
        temp = Object.keys(arrData[i])
        masGsx = []
        for (var j = 0; j < temp.length; j++) {
          if (myRe.test(temp[j])) {
            myRe.lastIndex = 0
            masGsx.push(temp[j])
          }
        }
        ItogObg[i] = masGsx
      }
      return ItogObg
    }
  }
}
</script>
<style>
  input[type='button']{
    font-size: 20px;
    margin: 30px;
    padding: 5px 15px;
    border-radius: 5px;
    background-color: #01a455;
    box-shadow: 0 2px 0 0 #057b42;
    border: none;
    color: white;
    cursor: pointer;
  }
  input[type='button']:hover{
    background-color: #15c26e;
    transition: all 300ms ease-in-out;
  }

  #content{
    display: flex;
  }
</style>
